
import { defineComponent } from 'vue'

import { NResult, NButton } from 'naive-ui'
import { useRouter } from 'vue-router'

export default defineComponent({
  name: 'NotFound',

  components: {
    NResult,
    NButton
  },

  setup() {
    const router = useRouter()
    const goHome = () => router.push({ name: 'Home' })

    return {
      goHome
    }
  }
})
