
import { computed, defineComponent } from 'vue'

import {
  NDrawer,
  NDrawerContent,
  NButton,
  NIcon,
  NAlert,
  NStatistic
} from 'naive-ui'
import { ShoppingBasket } from '@vicons/fa'
import { useCart } from '@/store/cart'
import CartList from './CartList.vue'
import { useRouter } from 'vue-router'

export default defineComponent({
  name: 'CartDrawer',

  props: {
    open: {
      required: true,
      type: Boolean
    }
  },

  emits: {
    'update:open': (value: boolean) => typeof value === 'boolean'
  },

  components: {
    NDrawer,
    NDrawerContent,

    NButton,
    NIcon,

    NAlert,
    NStatistic,
    ShoppingBasket,
    CartList
  },

  setup(props, context) {
    const router = useRouter()
    const cart = useCart()

    const basket = computed(() => cart.basket)
    const total = computed(() => cart.totalPrice)

    const empty = computed(() => cart.totalItems === 0)

    const show = computed({
      get() {
        return props.open
      },
      set(open: boolean) {
        context.emit('update:open', open)
      }
    })

    const checkout = () => {
      router.push({ name: 'Checkout' })
      show.value = false
    }

    return {
      basket,
      empty,
      total,
      show,
      checkout
    }
  }
})
